.sr-only { border: 0; clip: rect(0, 0, 0, 0); height: 1px; margin: -1px; overflow: hidden; padding: 0; position: absolute; width: 1px; }

.sr-only-focusable:active, .sr-only-focusable:focus { clip: auto; height: auto; margin: 0; overflow: visible; position: static; width: auto; }

footer { z-index: 2; }

.footer { background-color: #282828; padding: 84px 0 74px; }

.footer .wrapper { display: flex; justify-content: space-between; align-items: stretch; flex-direction: row; }

@media screen and (max-width: 600px) { .footer .wrapper { display: flex; justify-content: space-between; align-items: stretch; flex-direction: column; } }

@media screen and (max-width: 768px) { .footer { padding: 84px 0 50px; } }

@media screen and (max-width: 480px) { .footer { padding: 36px 0 50px; } }

.block { display: flex; justify-content: space-between; align-items: stretch; flex-direction: column; width: 55%; }

@media screen and (max-width: 768px) { .block { width: 45%; } }

@media screen and (max-width: 600px) { .block { width: 100%; } }

.block-info { display: flex; justify-content: space-between; align-items: stretch; flex-direction: row; width: 100%; }

@media screen and (max-width: 960px) { .block-info { flex-wrap: wrap; } }

@media screen and (max-width: 768px) { .block-info { width: 100%; } }

@media screen and (max-width: 600px) { .block-info { width: 100%; margin-bottom: 0; } }

@media screen and (max-width: 960px) { .block-info__item { width: 49%; } }

